export const  excluirUsuario = async (id) => {
    try {
        const response = await fetch(`http://localhost/app/Usuarios.php?id=${id}`, {
            method: 'DELETE',
        });

        const data = await response.json();
        console.log('Usurio excludo:', data);
    } catch (error) {
        console.error('Erro ao excluir usurio:', error);

    }
};
export const registrarUsuario = async (usuario) => {
    try {
        const response = await fetch('http://localhost/app/Usuarios.php', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ ...usuario, acao: 'registrar' }),
        });

        const data = await response.json();
        if(data.status){
            console.log('Registrado com sucesso');
            const sucesso = {
                ...data,
                message: 'Cadastrado com sucesso'
            };
            return sucesso
        }else{
            const sucesso = {
                ...data,
                message: 'J existe registro para o usurio'
            };
            return sucesso
        }
        
    } catch (error) {
        console.error('Erro ao registrar usurio:', error);
        return error
    }
};
export const fazerLogin = async (credenciais) => {
    try {
        const response = await fetch('http://localhost/app/Usuarios.php', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({ ...credenciais, acao: 'login' }),
        });

        const data = await response.json();
        if(data.status){
            console.log('Logado com sucesso');
            const sucesso = {
                ...data,
                message: 'Logado com sucesso'
            };
            return sucesso
        }else{
            const sucesso = {
                ...data,
                message: 'No  possivel logar'
            };
            return sucesso
        }
       
    } catch (error) {
        console.error('Erro ao fazer login:', error);
        return error
    }
};
